import Vue from 'vue';
import Router from 'vue-router';
import error404 from './views/404.vue';
import Home from './views/Home.vue';

Vue.use(Router);
Vue.component('router-link', Vue.options.components.RouterLink);
Vue.component('router-view', Vue.options.components.RouterView);

let router = new Router({
	routes: [{
			path: '*',
			component: error404,
			meta: {
				title: 'Nothing found',
			}
		},
		{
			path: '/',
			component: Home,
			meta: {
				title: 'Hello',
			}
		},
		{
			path: '/profile/',
			component: () => import('./views/Profile/Profile.vue'),
			redirect: '/profile/business-summary/',
			meta: {
				title: 'Profile',
			},
			children: [{
					path: 'business-summary',
					component: () => import('./views/Profile/BusinessSummary.vue'),
					props: {
						pageTitle: 'Business Summary'
					},
					meta: {
						title: "Business Summary"
					}
				},
				{
					path: 'professional-license',
					component: () => import('./views/Profile/ProfessionalLicense.vue'),
					props: {
						pageTitle: 'Professional License'
					},
					meta: {
						title: "Professional License"
					}
				},
				{
					path: 'reviews',
					component: () => import('./views/Profile/Reviews.vue'),
					props: {
						pageTitle: 'Reviews'
					},
					meta: {
						title: "Reviews"
					}
				},
				{
					path: 'photo-video',
					component: () => import('./views/Profile/PhotoVideo.vue'),
					props: {
						pageTitle: 'Photo & Video'
					},
					meta: {
						title: "Photo and Video"
					}
				},
				{
					path: 'general-info',
					component: () => import('./views/Profile/GeneralInfo.vue'),
					props: {
						pageTitle: 'General Info'
					},
					meta: {
						title: "General Info"
					}
				}
			]
		},
		{
			path: '/services/',
			component: () => import('./views/Services/Services.vue'),
			meta: {
				title: 'Services',
			}
		},
		{
			path: '/services/:id',
			component: () => import('./views/Services/Service.vue'),
			redirect:'/services/:id/service-info',
			meta: {
				title: 'Service',
			},
			children: [{
					path: 'service-info',
					component: () => import('./views/Services/ServiceInfo.vue'),
					props: {
						pageTitle: 'Service Info'
					},
					meta: {
						title: "Service Info"
					}
				},
				{
					path: 'service-inventory',
					component: () => import('./views/Services/ServiceInventory.vue'),
					props: {
						pageTitle: 'Service Inventory'
					},
					meta: {
						title: "Service Inventory"
					}
				},
				{
					path: 'service-price',
					component: () => import('./views/Services/ServicePrice.vue'),
					props: {
						pageTitle: 'Service Price'
					},
					meta: {
						title: "Service Price"
					}
				},
				{
					path: 'service-faq',
					component: () => import('./views/Services/ServiceFAQ.vue'),
					props: {
						pageTitle: 'Service FAQ'
					},
					meta: {
						title: "Service FAQ"
					}
				},
				{
					path: 'service-team',
					component: () => import('./views/Services/Team.vue'),
					props: {
						pageTitle: 'Service Team'
					},
					meta: {
						title: "Service Team"
					}
				}
			]
		}

	],
	mode: 'history',
	base: '/app/',
});
router.beforeEach((to, from, next) => {
	document.title = to.meta.title;
	next();
});

export default router;